import React,{useState, useEffect} from "react";
// https://fluentsite.z22.web.core.windows.net/quick-start
import { Provider, teamsTheme, Loader } from "@fluentui/react-northstar";
import { Routes, Route } from "react-router-dom";
import { useTeamsFx } from "./sample/lib/useTeamsFx";
import Privacy from "./Privacy";
import TermsOfUse from "./TermsOfUse";
import Tab from "./Tab";
import "./App.css";
import TabConfig from "./TabConfig";
import Auth from './Auth/Auth';
import { useSelector, useDispatch } from 'react-redux';
import {
  actionGetTodoList
} from '../app/Reducers/todo/todoSlice';

import Planning from '../pages/Planning/Planning';
import TaskView from '../pages/TaskView/TaskView';
import Dashboard from "../pages/Dashboard/Dashboard";
import TeamView from "../pages/TeamView/TeamView";
import BigBroSuggestion from "../pages/BigBroSuggestion/BigBroSuggestion";

/**
 * The main app which handles the initialization and routing
 * of the app.
 */
export default function App() {

  const { theme, loading } = useTeamsFx();
  const dispatch = useDispatch()
  const {userData} = useSelector(state => state.user); 
  const [isTaskView, setIsTaskView] = useState(true)

  useEffect(() => {
    if(userData)
      dispatch(actionGetTodoList(userData._id))
  }, [userData])
        
  return (
    <Provider theme={theme || teamsTheme} 
    // styles={{ backgroundColor: "#eeeeee" }}
    >
      {!userData ? 
        <Auth/>
        : 
        <Routes>
          <Route path="/" element={<Tab />} >
            <Route path="planning" element={<Planning />} />
            <Route path="tasks" element={<TaskView />} />
            <Route path="dashboard" element={<Dashboard />} />
            <Route path="teamview" element={<TeamView />} />
            <Route path="bigbrosuggestion" element={<BigBroSuggestion />} />
          </Route>
          {/* <Route to="/tab" path="/">
            <Redirect to="/tab" />
          </Route>
          {loading ? (
            <Loader style={{ margin: 100 }} />
          ) : (
            <>
              <Route path="/privacy" component={Privacy} />
              <Route path="/termsofuse" component={TermsOfUse} />
              <Route path="/tab" component={Tab} />
              <Route path="/config" component={TabConfig} />
            </>
          )} */}
        </Routes>
      }
    </Provider>
  );
}
