import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import Axios from "axios";
import * as microsoftTeams from "@microsoft/teams-js";

/* Just a test code... remove it afterwards */ 
const URI = "https://reflect-backend-2.azurewebsites.net";//process.env.DEV_MONGO_URL;
/* Just a test code... remove it afterwards */
const token = localStorage.getItem('wstoken');
const headers = {
    headers: {
        "auth-token": token
    }
}

const initialState = {
    noteList: [],
    notesFor: ""
};

export const userSlice = createSlice({
    name: 'note',
    initialState,
    reducers: {
        createNote: (state,action) => {
            state.noteList = [...state.noteList, action.payload]
        },
        updateNote: (state,action) => {
            const updatedNote = action.payload;
            state.noteList = [...state.noteList.map(note=>{
                return note._id === updatedNote._id ? updatedNote : note
            })]
        },
        setNotes: (state, action) => {
            state.noteList = action.payload
        },
        setNotesFor: (state, action) => {
            state.notesFor = action.payload
        }
    }
});

export const {
    createNote,
    updateNote,
    setNotes,
    setNotesFor
} = userSlice.actions;

export const actionCreateNotes = createdNoteData => (dispatch,getState) => {
    const {user} = getState();
    const userId = user.userData ? user.userData._id : null;
    Axios.post(
        `${URI}/notes/create`,
        {
            ...createdNoteData,
            userId
        },
        headers
    ).then(res=>{
        dispatch(createNote(res.data))
    })
}
export const actionUpdateNotes = updatedNote => dispatch => {
    Axios.put(
        `${URI}/notes/update`,
        {...updatedNote},
        headers
    ).then(res => {
        dispatch(updateNote(res.data))
    })
}
export const actionDeleteNotes = noteId => dispatch => {
    Axios.delete(
        `${URI}/notes/${noteId}`,
        headers
    ).then(res => {
        // setNoteList([...noteList.filter(note => note._id !== noteId)])
    })
}
export const actionGetNotes = () => dispatch => {
    Axios.get(
        `${URI}/notes`,
        headers
    ).then(res => {
        dispatch(setNotes(res.data))
    })
}
export const actionSetNotesFor = notesFor => dispatch => {
    dispatch(setNotesFor(notesFor))
}

