//import TodoList from '.components/TodoList/TodoList';//cut it and paste to the component
import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import './TodoList.scss';
import Todo from './Todo';//cut it and paste to the component
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import CreateTodo from './CreateTodo';
import { ReactComponent as RefreshIcon } from '../../Assets/ic_refresh.svg';
import { ReactComponent as SortIcon } from '../../Assets/ic_sort.svg';
import { useSelector, useDispatch } from 'react-redux';
import {
    actionGetTodoList
} from '../../app/Reducers/todo/todoSlice';
import Tooltip from '@mui/material/Tooltip';

const TodoList = ({
    todoList,
    appendSchedule
}) => {

    const dispatch = useDispatch()
    
    const {isTodoListLoading} = useSelector(state => state.todo); 
    const {userData} = useSelector(state => state.user); 
    const { thirdPartyUserData } = useSelector(state => state.user);
    const [createNotesOpen, setCreateNotesOpen] = useState(false)

    return <div className='TodoListContainer'>
        <div className="todo-heading-container" onClick={()=>console.log(thirdPartyUserData)}>
            <Stack direction="row" alignItems="center">
                <h2>Todo List</h2>

                <Tooltip title="Refresh">
                    <IconButton
                    sx={{ marginLeft: "10", padding: "5px"}}
                    aria-label="refresh"
                    onClick={()=>dispatch(actionGetTodoList(userData._id))}>
                        <RefreshIcon />
                    </IconButton>
                </Tooltip>

                <Tooltip title="Add Task">
                    <IconButton
                    sx={{ marginLeft: "10", padding: "5px"}}
                    aria-label="add"
                    onClick={()=>setCreateNotesOpen(true)}>
                        <AddIcon />
                    </IconButton>
                </Tooltip>

                <Tooltip title="Sort by priority">
                    <IconButton
                    sx={{ marginLeft: "10", padding: "5px"}}
                    aria-label="sort"
                    onClick={()=>{}}>
                        <SortIcon />
                    </IconButton>
                </Tooltip>
            </Stack>
        </div>

        <CreateTodo
            createNotesOpen={createNotesOpen}
            setCreateNotesOpen={setCreateNotesOpen}/>

        {
        isTodoListLoading ? "Loading..." : 
            todoList.filter(todo=>todo.status != 3).map(todo =><Todo
                            key={todo._id}
                            todo={todo}
                            appendSchedule={appendSchedule}/>
            )
        }
    </div>
}

export default TodoList;