export const actionGetAverageCountData = (userId, urlParam = "") => async dispatch => {

    const token = localStorage.getItem('wstoken');
    if(!token){ return 0 }

    const res = await Axios.get(
                    `${URI}/calendar/calendar-get-week-average-comparison/${userId}?${urlParam}`,
                    headers
                )
    console.log(res)
    dispatch(setWeeklyProductivity(res.data))

}
