import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import {v1 as uuid} from "uuid";
import EditTodo from "./EditTodo";
import DeleteTodo from "./DeleteTodo"

const TodoMoreOptions = ({
    todo
}) => {

    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const [editTodoOpen, setEditTodoOpen] = useState(false);
    const [deleteConfirmationOpen, setDeleteConfirmationOpen] = useState(false);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => setAnchorEl(null);

    const menuOptions = [
        {
            id: uuid(),
            name: "Edit",
            icon: <EditIcon fontSize="small"/>,
            callback: () => setEditTodoOpen(true)
        },
        {
            id: uuid(),
            name: "Delete",
            icon: <DeleteIcon fontSize="small"/>,
            callback: () => setDeleteConfirmationOpen(true)
        }
    ];

    const getMenuItems = () => {
        return menuOptions.map(option=>(
            <MenuItem
            key={option.id}
            onClick={()=>{
                option.callback()
                setAnchorEl(null)
            }}>
                <ListItemIcon>
                    {option.icon}
                </ListItemIcon>
                <ListItemText>{option.name}</ListItemText>
            </MenuItem>
        ))
    }
    
    return <div className='TodoMoreOptionsContainer'>
        {/* <EditTodo/> */}
        <IconButton
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}>
            <MoreHorizIcon/>
        </IconButton>
        <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{
            'aria-labelledby': 'basic-button',
            }}
        >
            {getMenuItems()}
        </Menu>
        <EditTodo
            todo={todo}
            editTodoOpen={editTodoOpen}
            setEditTodoOpen={setEditTodoOpen}
        />
        <DeleteTodo
            todo={todo}
            deleteConfirmationOpen={deleteConfirmationOpen}
            setDeleteConfirmationOpen={setDeleteConfirmationOpen}
        />
    </div>
}

export default TodoMoreOptions;