import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import PauseCircleOutlineIcon from '@mui/icons-material/PauseCircleOutline';
import StopCircleIcon from '@mui/icons-material/StopCircle';
import { useDispatch } from 'react-redux';

import {
    actionPauseTodo,
    actionResumeTodo,
    actionEndTodo,
    actionStartTodo
} from '../../app/Reducers/todo/todoSlice';

const TodoActions = ({
    todo
}) => {

    const dispatch = useDispatch();
    
    const onStartEvent = () => {
        dispatch(actionStartTodo(todo))
    }
    const onEndEvent = () => {
        dispatch(actionEndTodo(todo._id))
    }
    const onPauseEvent = () => {
        dispatch(actionPauseTodo(todo._id))
    }
    const onResumeEvent = () => {
        dispatch(actionResumeTodo(todo))
    }

    return <div className="actions-container">
        {
                todo?.status === 1 ? (
                    <Stack direction="row" alignItems="center" spacing={1}>
                        <IconButton onClick={()=>onPauseEvent()} aria-label="pause" size="medium">
                            <PauseCircleOutlineIcon fontSize="inherit" />
                        </IconButton>
                        <IconButton onClick={()=>onEndEvent()} aria-label="delete" size="medium">
                            <StopCircleIcon fontSize="inherit" />
                        </IconButton>
                    </Stack>
                ) :
                todo?.status === 0 ? (
                    <IconButton onClick={()=>onStartEvent()} aria-label="start" size="medium">
                        <PlayCircleOutlineIcon
                        fontSize="inherit"
                        sx={{ color: "#00AB55" }} />
                    </IconButton>
                ) :
                todo?.status === 2 ? (
                    <Stack direction="row" alignItems="center" spacing={1}>
                        <IconButton onClick={()=>onResumeEvent()} aria-label="resume" size="medium">
                            <PlayCircleOutlineIcon
                            fontSize="inherit"
                            sx={{ color: "#00AB55" }}/>
                        </IconButton>
                        <IconButton onClick={()=>onEndEvent()} aria-label="delete" size="medium">
                            <StopCircleIcon fontSize="inherit" />
                        </IconButton>
                    </Stack>
                ) : ""
            }
    </div>
}

export default TodoActions;