import React, {useState,useEffect,Fragment,useRef,memo, useCallback} from 'react';
import './Todo.scss';
import TodoMoreOptions from './TodoMoreOptions';
import TodoActions from './TodoActions'
import Axios from "axios";
import { getTodoStatus } from '../Hooks/Constants';
import { useSelector, useDispatch } from 'react-redux';
import {
    actionSetTodoInSideNav
} from '../../app/Reducers/todo/todoSlice';
import { ReactComponent as StarIcon } from '../../Assets/ic_star.svg';

const Todo = ({
    todo
}) => {

    const dispatch = useDispatch();
    const checktodo = useCallback((event) => {
        console.log(todo)
    },[]);
    const [partialDesc, setPartialDesc] = useState(true);
    
    const statusData = getTodoStatus(todo?.status);

    return <div className='TodoContainer'>
        <ul className="tags">
            <li>
                <span onClick={checktodo}
                className="tag status"
                style={{
                    color: statusData.color,
                    background: statusData.background
                }}>{statusData.text}</span>
            </li>
            <li>
                <span onClick={checktodo} className="tag priority">
                    <span className="icon-left">
                        <StarIcon/> 
                    </span>
                    <span className="text">
                        P {todo?.priorityLevel}
                    </span>
                </span>
            </li>
        </ul>
        <div className="todo-info">
            <p
            className="todo-name"
            onClick={() => {
                dispatch(actionSetTodoInSideNav(todo._id))
            }}>{todo.name}</p>
            {todo?.description?.length ? <>
                <p className={`todo-description ${partialDesc ? "one-line" : ""}`}>{todo.description}</p>
            <p
            className="todo-more-trigger"
            onClick={()=>setPartialDesc(!partialDesc)}
            >{partialDesc ? "More..." : "Less..."}</p>
            </> : ""}
        </div>
        <TodoActions todo={todo}/> 
        <TodoMoreOptions todo={todo}/>
    </div>
}

export default Todo;