export const actionGetCalendarData = date => async dispatch => {
    
    if(!token){ return 0 }

    const calRes = await Axios.get(`${URI}/calendar/get-calendar-on-date/${date}`,headers)
    const breakCalRes = await Axios.get(`${URI}/break/get-break-on-date/${date}`,headers)

    const overAllCalendarData = [
        ...calRes.data.map(schedule=>{
            return {
            ...schedule,
            id: schedule._id
            }
        }),
        ...breakCalRes.data.map(schedule=>{
            return {
            ...schedule,
            id: schedule._id
            }
        })
    ]

    dispatch(setCalendarSchedules(overAllCalendarData))
}

